/* NAVIGATION MENU */
.navigation-menu-container ul {
  text-decoration: none !important;
  list-style: none !important;
  padding-left: 0 !important;
}
.navigation-menu-container ul li {
  padding-bottom: 1rem;
  padding-top: 1rem;
  text-decoration: none;
  color: #f8f8f8;
  display: block;
  border: none;
  background: none;
  width: 100%;
  text-align: left;
  cursor: pointer;
  outline: none;
  font-weight: 400;
  line-height: 1.75;
  font-size: 14px;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  letter-spacing: 0.6px;
  /* margin: auto auto !important; */
}
.navigation-menu-container ul :hover {
  background-color: #bac1b8;
  /* color: #8a85ff; */
  color: #0c7c59;
}

.nav-links {
  text-decoration: none !important;
  color: #616161;
  margin-bottom: 1rem;
  cursor: pointer;
  padding-left: 2rem;
  display: flex;
}
.nav-links img {
  padding-right: 1rem;
}
.navigation-menu-heading {
  text-align: center;
  padding: 1rem;
  background-color: #2b303a;
  color: aliceblue;
  font-weight: 700;
}
.sidebar-footer {
  position: fixed;
  bottom: 0;
  background-color: #2b303a;
  width: 100%;
  height: 3.9rem;
  text-align: center;
  padding-bottom: 1rem;
  color: rgb(255, 255, 255);
  font-weight: 600;
}

.footer-container {
  width: 100%;
  padding-bottom: 30px;
  position: absolute;
  bottom: 0px;
  left: 0px;
  text-align: center;
  color: #0c7c59;
  padding-top: 1.5rem;
  /* background-color: #edf2f7; */
  background-color: #2b303a;
  /* margin: 0 auto; */
}

